import { GetWithParams, Post, Patch } from "../CRUD"

// 登入 INFO使用
export function login(data) {
  return Post(`/api/user/login`, data, true)
}

// Request with Params
export function RP(token) {
  return GetWithParams(`/api/user/info`, { token })
}

export function U(data) {
  return Patch(`/api/user/password/self`, data, true)
}
