import router, { asyncRoutes } from "@/router"
import localCache from "@/utils/cache"
import settings from "@/settings"
import { ElMessage } from "element-plus"

import { login, RP, U } from "@/service/api/user"

const defaultUser = () => {
  return { token: "", userInfo: {} }
}

const resetRouter = () => {
  const asyncRouterNameArr = asyncRoutes.map((menuItem) => menuItem.name)
  asyncRouterNameArr.forEach((name) => {
    if (router.hasRoute(name)) {
      router.removeRoute(name)
    }
  })
}

const state = () => {
  return {
    token: localCache.get("token") ?? "",
    userInfo: {},
    roles: localCache.getLocal("roles") ?? []
  }
}
const mutations = {
  SET_TOKEN: (state, token) => {
    state.token = token
  },
  SET_USER_INFO: (state, userInfo) => {
    state.userInfo = userInfo
  },
  SET_ROLES: (state, roles) => {
    state.roles = roles
  },
  RESET_ALL: (state) => {
    Object.assign(state, defaultUser())
  }
}

const actions = {
  async loginAction({ commit }, payload) {
    // 1. 登入
    if (settings.isNeedLogin) {
      const res = await login(payload)
      if (res?.status === true) {
        localCache.set("token", res.data.token)
        ElMessage.success(res.msg)
        await commit("SET_TOKEN", res.data.token)
        // 2. 跳轉
        router.push({ path: state.redirect || "/" })
      }
    }
  },

  // getUserInfo
  getUserInfoAction({ commit, state }) {
    return new Promise((resolve, reject) => {
      RP(state.token)
        .then((res) => {
          commit("SET_USER_INFO", res.data)
          commit("SET_ROLES", res.data.roles)
          resolve(res.data)
        })
        .catch((error) => {
          reject(error)
        })
    })
  },

  // setUserInfo
  setUserInfoAction({ commit }, { userInfo, roles }) {
    return new Promise((resolve) => {
      commit("SET_USER_INFO", userInfo)
      // 紀錄 roles
      localCache.setLocal("roles", roles)
      commit("SET_ROLES", roles)
      resolve(userInfo)
    })
  },

  // resetCache
  // eslint-disable-next-line no-empty-pattern
  setPasswordAction({}, payload) {
    return new Promise((resolve, reject) => {
      U(payload)
        .then((res) => {
          ElMessage.success(res.msg)
          resolve(res.msg)
        })
        .catch((error) => {
          reject(error.msg)
        })
    })
  },

  // noNeedLogin
  setTokenAction({ commit }, token) {
    localCache.set("token", token)
    commit("SET_TOKEN", token)
  },

  // logout
  resetUser({ commit }) {
    return new Promise((resolve) => {
      localCache.remove("token")
      localCache.removeLocal("roles")
      localCache.removeLocal("routes")
      localCache.removeLocal("addRoutes")
      resetRouter()
      commit("RESET_ALL")
      resolve()
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
