import { C, R, U, UN, UP, D } from "@/service/api/users"

const defaultLogs = {
  fullUsers: [],
  users: [],
  listLoading: false
}

const state = () => defaultLogs

const mutations = {
  SET_USERS: (state, users) => {
    users.forEach((u, index) => {
      if (u.roles?.some((d) => d.includes("admin"))) {
        users[index]["checkedPage"] = ["admin"]
      } else {
        let checked = []
        ;["user", "clinic"].forEach((d) => {
          const temp = u.roles.filter((a) => a.match(d))
          if (temp) checked.push(temp)
        })
        users[index]["checkedPage"] = checked
      }
    })
    state.fullUsers = users
    state.users = JSON.parse(JSON.stringify(users))
  },

  SET_FILTER_USERS: (state, users) => {
    state.users = users
  },

  SET_LISTLOADING: (state) => {
    console.log(state.listLoading)
    state.listLoading = !state.listLoading
  }
}

const actions = {
  getUsers({ commit }) {
    return new Promise((resolve, reject) => {
      commit("SET_LISTLOADING")
      R()
        .then((res) => {
          commit("SET_USERS", res.data)
          resolve(res.data)
        })
        .catch((e) => {
          reject(e)
        })
        .finally(() => {
          commit("SET_LISTLOADING")
        })
    })
  },

  filterUsers({ commit, state }, payload) {
    return new Promise((resolve) => {
      commit("SET_LISTLOADING")
      const users = state.fullUsers.filter((a) => a.account.match(payload.word))
      commit("SET_FILTER_USERS", users)
      resolve(users)
      commit("SET_LISTLOADING")
    })
  },

  noFilterUsers({ commit, state }) {
    return new Promise((resolve) => {
      commit("SET_LISTLOADING")
      const users = JSON.parse(JSON.stringify(state.fullUsers))
      commit("SET_FILTER_USERS", users)
      resolve(users)
      commit("SET_LISTLOADING")
    })
  },

  insertUsers({ commit, dispatch }, payload) {
    return new Promise((resolve, reject) => {
      commit("SET_LISTLOADING")
      C(payload)
        .then((res) => {
          dispatch("getUsers")
          resolve(res)
        })
        .catch((e) => {
          reject(e)
        })
        .finally(() => {
          commit("SET_LISTLOADING")
        })
    })
  },

  updateUsers({ commit, dispatch }, payload) {
    return new Promise((resolve, reject) => {
      commit("SET_LISTLOADING")
      UN(payload)
        .then((res) => {
          dispatch("getUsers")
          resolve(res)
        })
        .catch((e) => {
          reject(e)
        })
        .finally(() => {
          commit("SET_LISTLOADING")
        })
    })
  },

  updateUsersAccount({ commit, dispatch }, payload) {
    return new Promise((resolve, reject) => {
      commit("SET_LISTLOADING")
      U(payload)
        .then((res) => {
          dispatch("getUsers")
          resolve(res)
        })
        .catch((e) => {
          reject(e)
        })
        .finally(() => {
          commit("SET_LISTLOADING")
        })
    })
  },

  updateUsersPassword({ commit, dispatch }, payload) {
    return new Promise((resolve, reject) => {
      commit("SET_LISTLOADING")
      UP(payload)
        .then((res) => {
          dispatch("getUsers")
          resolve(res)
        })
        .catch((e) => {
          reject(e)
        })
        .finally(() => {
          commit("SET_LISTLOADING")
        })
    })
  },

  deleteUsers({ commit, dispatch }, payload) {
    return new Promise((resolve, reject) => {
      commit("SET_LISTLOADING")
      D(payload)
        .then((res) => {
          dispatch("getUsers")
          resolve(res)
        })
        .catch((e) => {
          reject(e)
        })
        .finally(() => {
          commit("SET_LISTLOADING")
        })
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
