import { computed } from "vue";
import { useStore } from "vuex";
import { useRoute } from "vue-router";
import MenuItem from "./MenuItem.vue";
import Logo from "./Logo.vue";
export default {
  components: {
    MenuItem,
    Logo
  },

  setup() {
    const store = useStore();
    const route = useRoute();
    const routes = computed(() => store.getters.routes);
    const activeMenu = computed(() => {
      const {
        meta,
        path
      } = route;
      if (meta.activeMenu) return meta.activeMenu;
      return path;
    });
    const isCollapse = computed(() => store.getters.menuToggle);
    const settings = computed(() => store.getters.settings); // 如果手機螢幕大小的話，Menu關閉就不要顯示ICON

    const asideWidth = computed(() => isCollapse.value ? settings.value.closedWindowHeight : settings.value.openedWindowHeight);
    return {
      routes,
      activeMenu,
      isCollapse,
      settings,
      asideWidth
    };
  }

};