import { ref } from "vue";
import { isExternal } from "@/utils/validate";
import { resolve } from "@/utils/utils";
import Link from "./Link.vue";
export default {
  components: {
    Link
  },
  props: {
    item: {
      type: Object,
      required: true
    },
    isNest: {
      type: Boolean,
      default: false
    },
    basePath: {
      type: String,
      default: ""
    }
  },

  setup(props) {
    // for迴圈遍歷MENU用的變數
    const child = ref(null); // 判斷是否要顯示該MenuItem

    const showMenuItem = (children = [], parent) => {
      // 將
      const hasChildren = children.filter(item => child.value = item); // 如果children只有一個

      if (hasChildren.length === 1) return true; // 如果沒有children

      if (hasChildren.length === 0) {
        child.value = { ...parent,
          path: "",
          hasNoChildren: true
        };
        return true;
      }

      return false;
    }; // 路徑判斷是否為外部連結


    const resolvePath = routePath => {
      if (isExternal(routePath)) {
        return routePath;
      }

      if (isExternal(props.basePath)) {
        return props.basePath;
      }

      return resolve(props.basePath, routePath);
    };

    return {
      showMenuItem,
      child,
      resolvePath
    };
  }

};