/**
 * @param {Array} paths
 * @returns {string}
 */
export function resolve(...paths) {
  let resolvePath = ""
  let isAbsolutePath = false
  for (let i = paths.length - 1; i > -1; i--) {
    let path = paths[i]
    if (isAbsolutePath) break
    if (!path) continue
    resolvePath = path + "/" + resolvePath
    isAbsolutePath = path.charCodeAt(0) === 47
  }
  if (/^\/+$/.test(resolvePath)) resolvePath = resolvePath.replace(/(\/+)/, "/")
  else {
    resolvePath = resolvePath
      .replace(/(?!^)\w+\/+\.{2}\//g, "")
      .replace(/(?!^)\.\//g, "")
      .replace(/\/+$/, "")
  }
  return resolvePath
}

/**
 * @param {string} url
 * @param {Array} params
 * @param {string} winName
 * @returns {void}
 */
export function openNewWindowWithPost(
  url = "",
  params = {},
  winName = "預覽畫面"
) {
  const winURL = url
  var windowoption =
    "resizable=yes,height=600,width=800,location=0,menubar=0,scrollbars=1"
  var form = document.createElement("form")
  form.setAttribute("method", "post")
  form.setAttribute("action", `/api/preview${winURL}`)
  form.setAttribute("target", winName)
  console.log(params)
  for (var i in params) {
    if (Object.prototype.hasOwnProperty.call(params, i)) {
      console.log(i)
      var input = document.createElement("input")
      input.type = "hidden"
      input.name = i
      input.value = params[i]
      form.appendChild(input)
    }
  }
  document.body.appendChild(form)
  window.open("", winName, windowoption)
  form.target = winName
  form.submit()
  document.body.removeChild(form)
}
