import { Get, Post, Patch, Put, Delete } from "../CRUD"

// CRUD 使用者

export function R() {
  return Get(`/api/user`)
}

export function C(data) {
  return Post(`/api/user`, data)
}

export function U(data) {
  return Put(`/api/user`, data)
}
export function UN(data) {
  return Patch(`/api/user`, data)
}

export function UP(data) {
  return Patch(`/api/user/password`, data)
}

export function D(data) {
  return Delete(`/api/user`, data)
}
