import { C, R, U, UT, D } from "@/service/api/clinic"

const defaultClinic = {
  fullClinics: [],
  clinics: [],
  listLoading: false
}

const state = () => defaultClinic

const mutations = {
  SET_CLINICS: (state, clinics) => {
    state.fullClinics = clinics
    state.clinics = JSON.parse(JSON.stringify(clinics))
  },

  SET_FILTER_CLINICS: (state, clinics) => {
    state.clinics = clinics
  },

  SET_LISTLOADING: (state) => {
    state.listLoading = !state.listLoading
  }
}

const actions = {
  getClinics({ commit }) {
    return new Promise((resolve, reject) => {
      commit("SET_LISTLOADING")
      R()
        .then((res) => {
          commit("SET_CLINICS", res.data)
          resolve(res.data)
        })
        .catch((e) => {
          reject(e)
        })
        .finally(() => {
          commit("SET_LISTLOADING")
        })
    })
  },

  filter({ commit, state }, payload) {
    return new Promise((resolve) => {
      commit("SET_LISTLOADING")
      const clinics = state.fullClinics.filter(
        (a) => a.name.match(payload.word) || a.token.match(payload.word)
      )
      commit("SET_FILTER_CLINICS", clinics)
      resolve(clinics)
      commit("SET_LISTLOADING")
    })
  },

  noFilter({ commit, state }) {
    return new Promise((resolve) => {
      commit("SET_LISTLOADING")
      const clinics = JSON.parse(JSON.stringify(state.fullClinics))
      commit("SET_FILTER_CLINICS", clinics)
      resolve(clinics)
      commit("SET_LISTLOADING")
    })
  },

  updateClinic({ commit, dispatch }, payload) {
    return new Promise((resolve, reject) => {
      commit("SET_LISTLOADING")
      U(payload)
        .then((res) => {
          dispatch("getClinics")
          resolve(res)
        })
        .catch((e) => {
          reject(e)
        })
        .finally(() => {
          commit("SET_LISTLOADING")
        })
    })
  },

  updateClinicToken({ commit, dispatch }, payload) {
    return new Promise((resolve, reject) => {
      commit("SET_LISTLOADING")
      UT(payload)
        .then((res) => {
          dispatch("getClinics")
          resolve(res)
        })
        .catch((e) => {
          reject(e)
        })
        .finally(() => {
          commit("SET_LISTLOADING")
        })
    })
  },

  insertClinic({ commit, dispatch }, payload) {
    return new Promise((resolve, reject) => {
      commit("SET_LISTLOADING")
      C(payload)
        .then((res) => {
          dispatch("getClinics")
          resolve(res)
        })
        .catch((e) => {
          reject(e)
        })
        .finally(() => {
          commit("SET_LISTLOADING")
        })
    })
  },

  deleteClinic({ commit, dispatch }, payload) {
    return new Promise((resolve, reject) => {
      commit("SET_LISTLOADING")
      D(payload)
        .then((res) => {
          dispatch("getClinics")
          resolve(res)
        })
        .catch((e) => {
          reject(e)
        })
        .finally(() => {
          commit("SET_LISTLOADING")
        })
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
