import { Get, Post, Patch, Put, Delete } from "../CRUD"

export function C(data) {
  return Post(`/api/clinic`, data)
}

export function R() {
  return Get(`/api/clinic`)
}

export function U(data) {
  return Put(`/api/clinic`, data)
}

export function UT(data) {
  return Patch(`/api/clinic/token`, data)
}

export function D(data) {
  return Delete(`/api/clinic`, data)
}
