import { computed, watch } from "vue";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
import { Menu, Navbar, AppMain } from "./components/index.js";
export default {
  components: {
    Menu,
    Navbar,
    AppMain
  },

  setup() {
    const store = useStore();
    const showTopNavbar = computed(() => store.getters.settings.showTopNavbar); // 點擊非menu區域，關閉menu

    const clickOutside = () => {
      store.dispatch("settings/closeMenu");
    }; // 手機版監聽路由變化，每當改變路由就關閉menu


    const router = useRouter();
    watch(() => router.currentRoute.value.path, () => {
      if (device.value === "mobile") return store.dispatch("settings/closeMenu");
    });
    const device = computed(() => store.getters.device);
    const classObj = computed(() => {
      return {
        closeMenu: store.getters.menuToggle,
        hideMenu: !store.getters.settings.showLeftMenu || device.value === "mobile"
      };
    });
    const isCollapse = computed(() => !store.getters.menuToggle && device.value === "mobile");
    return {
      showTopNavbar,
      classObj,
      isCollapse,
      clickOutside
    };
  }

};