import { R, RPU } from "@/service/api/logs"

const defaultLogs = {
  fullLogs: [],
  logs: []
}

const state = () => defaultLogs

const mutations = {
  SET_LOGS: (state, logs) => {
    state.fullLogs = logs
    state.logs = JSON.parse(JSON.stringify(logs))
  },

  SET_FILTER_LOGS: (state, logs) => {
    state.logs = logs
  }
}

const actions = {
  getLogs({ commit }) {
    return new Promise((resolve, reject) => {
      R()
        .then((res) => {
          commit("SET_LOGS", res.data)
          resolve(res.data)
        })
        .catch((error) => {
          reject(error)
        })
    })
  },

  filterLogs({ commit }, payload) {
    return new Promise((resolve, reject) => {
      RPU(payload)
        .then((res) => {
          commit("SET_FILTER_LOGS", res.data)
          resolve(res.data)
        })
        .catch((error) => {
          reject(error)
        })
    })
  },

  noFilterLogs({ commit, state }) {
    return new Promise((resolve) => {
      const logs = JSON.parse(JSON.stringify(state.fullLogs))
      commit("SET_FILTER_LOGS", logs)
      resolve(logs)
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
