import { UserFilled } from "@element-plus/icons-vue"; // import { useRouter } from "vue-router"

import { useStore } from "vuex";
import { computed } from "vue";
import { ElMessage, ElMessageBox } from "element-plus";
export default {
  setup() {
    const store = useStore(); // // eslint-disable-next-line no-unused-vars
    // const router = useRouter()

    const userInfo = computed(() => store.getters.userInfo); // 處理登出

    const handleLogout = async () => {
      await store.dispatch("user/resetUser"); // router.push({ path: "/login", replace: true })

      location.reload();
    }; // 處理清除cache


    const handleCacheClear = async () => {
      await store.dispatch("user/resetCache", {});
    };

    const openMB = () => {
      const html = `
        <label id="p1_" for="p1" class="el-form-item__label">舊密碼</label>
        <div class="el-form-item__content">
          <div class="el-input">
            <div class="el-input__wrapper">
              <input class="el-input__inner" type="password" id="p1" />
            </div>
          </div>
        </div>
        <label id="p2_" for="p2" class="el-form-item__label">新密碼</label>
        <div class="el-form-item__content">
          <div class="el-input">
            <div class="el-input__wrapper">
              <input class="el-input__inner" type="password" id="p2" />
            </div>
          </div>
        </div>
        <label id="p3_" for="p3" class="el-form-item__label">密碼確認</label>
        <div class="el-form-item__content">
          <div class="el-input">
            <div class="el-input__wrapper">
              <input class="el-input__inner" type="password" id="p3" />
            </div>
          </div>
        </div>
      `;
      ElMessageBox.alert(html, "修改密碼", {
        confirmButtonText: "提交",
        cancelButtonText: "取消",
        showCancelButton: true,
        dangerouslyUseHTMLString: true
      }).then(async () => {
        const p1 = document.getElementById("p1").value;
        const p2 = document.getElementById("p2").value;
        const p3 = document.getElementById("p3").value;

        if (String(p1) === "" || String(p2) === "" || String(p3) === "") {
          ElMessage.error(`請輸入輸入框!`);
          return;
        }

        if (!(String(p2) !== "" && p2 === p3)) {
          ElMessage.error(`密碼輸入需要一致!`);
          return;
        }

        const data = {
          user_id: userInfo.value.user_id,
          oldpass: p1,
          password: p2,
          passconf: p3
        };
        await store.dispatch("user/setPasswordAction", { ...data
        });
      });
    };

    return {
      UserFilled,
      handleLogout,
      handleCacheClear,
      openMB,
      userInfo
    };
  }

};