import service from "./index"

// CRUD
export function Get(url, showLoading = false) {
  return service.get({
    url: url,
    showLoading: showLoading
  })
}

export function GetWithParams(url, params, showLoading = false) {
  return service.get({
    url: url,
    showLoading: showLoading,
    params: params
  })
}

export function Post(url, data, showLoading = false) {
  return service.post({
    url: url,
    showLoading: showLoading,
    data: data
  })
}

export function Patch(url, data, showLoading = false) {
  return service.patch({
    url: url,
    showLoading: showLoading,
    data: data
  })
}

export function Put(url, data, showLoading = false) {
  return service.put({
    url: url,
    showLoading: showLoading,
    data: data
  })
}

export function Delete(url, data, showLoading = false) {
  return service.delete({
    url: url,
    showLoading: showLoading,
    data: data
  })
}
